/*
white #EFEFEF
black #0F0F0F
dark grey #383838
yellow #FFF59B
red #9E1800

option bg #F3F3F3
option border #E9E9E9

review bg #EAEAEA
reviewborder #E2E2E2
*/

svg {
  max-width: 100px;
}

.icons {
  min-width: 40px;
}

/*====================
Global
=====================*/

body {
  background-color: #EFEFEF;
  color: #0F0F0F;
  font-family: 'Montserrat', sans-serif;
  font-size: .85rem;
}

.dimmed {
  opacity: .5;
}

ul, ol {
  padding: 0;
  list-style-type: none;
}

button {
  border: 1px solid #E9E9E9;
  border-radius: 5px;
  background-color: #F3F3F3;
}

  button:focus,
  input:focus {
    outline-width: 2px;
    outline-color: #FFF59B;
  }

/*====================
Navbar
=====================*/

nav {
  position: relative;
  display: grid;
  background-color: #383838;
  color: #EFEFEF;
}
  /* navigation */
  nav > ul {
    padding: 1vw;
    gap: 1vw;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 25% 1fr 25%;
  }

  #nav-categories {
    justify-items: start;
  }

    @media (max-width: 400px) {
      nav > ul {
        grid-template-columns: 25vw 45vw 25vw;
      }

      #nav-categories > button {
        justify-self: start;
      }
    }

      nav ol {
        display: grid;
      }

        nav ol > li,
        nav ol > li > button {
          background-color: #EFEFEF;
          color: #383838;
          text-align: center;
          padding: 5px;
        }

        nav .active.links > button {
          color: #FFF59B;
        }

    /* cart */
    nav > ul > li:nth-of-type(4) {
      justify-self: end;
    }

  nav a:active {
    color: #FFF59B;
  }

    #nav-categories {
      grid-row: 2;

      display: grid;
      gap: 1vw;
    }

    #nav-login,
    #nav-cart {
      grid-row: span 2;
    }

    /* signin/login form */
    nav form {
      display: grid;
      gap: 1.5vw;
      place-items: center;

      font-size: .75rem;
    }

      nav input[type="text"] {
        max-width: 30vw;
        font-size: .6rem;
      }

      /* sign in prompt */
      #sign-in-form > label {
        grid-column: span 2;
      }

      /* button before interacting */
      #sign-in-form > button:first-of-type {
        justify-self: end;
      }

      #sign-in-form > button:last-of-type {
        justify-self: start;
      }

      /* switch to other button link */

      #login-form > button:last-of-type,
      #sign-up-form > button:last-of-type {
        max-width: 60%;
      }

    /* button styles in nav field */
    nav :not(form) > button,
    nav #login-form > button:last-of-type,
    nav #sign-up-form > button:last-of-type {
      border: none;
      border-radius: 0;

      background-color: #383838;
      color: #EFEFEF;

      text-decoration: underline;
    }

    span.active {
      color: #FFF59B;
    }

    #cart-button-span {
      display: grid;
      place-items: end;
      gap: 1vw;
    }

      #cart-button-span > svg {
        width: 50%;
      }

/*====================
Cart
=====================*/

nav aside {
  display: grid;
  background-color: #EFEFEF;
}

#cart svg,
#car .icons {
  max-width: 50px;
}

.cart-products {
  display: grid;
}

  #cart button {
    background-color: #EFEFEF;
    color: #0F0F0F;
  }

  .cart-products {
    place-items: center;
  }

  .cart-products > img {
    grid-row: span 2;
  }

  .cart-products > h3,
  .cart-products > form {
    grid-column: 2;
  }

  #checkout-button {
    justify-self: center;
    margin: 1vw;
  }

    #checkout-button-span {
      display: grid;
      place-items: center;
      grid-auto-flow: column;
      padding: 2vw;

      border: 1px solid #E9E9E9;
      border-radius: 5px;
      background-color: #F3F3F3;
    }

      #checkout-button-span > svg {
        width: 50%;
      }

    #cart .quantities {
      display: grid;
      grid-auto-flow: column;
      place-items: center;
    }

  @media (max-width: 349px) {
    .cart-products {
      place-content: center;
      grid-auto-columns: 35vw;
    }

    #cart .cart-products img {
      width: 100%;
    }
  }

    @media (max-width: 320px) {
      #cart .cart-products img {
        width: 60%;
      }
    }

  @media (min-width: 350px) and (max-width: 599px) {
    #cart {
      width: 100vw;
      border-bottom: 5px solid #383838;
    }

    .cart-products {
      gap: 3vw;
      grid-auto-columns: minmax(40vw, 1fr);
    }

    .cart-products > img {
      justify-self: end;
      align-self: center;
    }

    .cart-products > h3,
    .cart-products > form {
      justify-self: start;
      align-self: center;
    }
  }

  @media (min-width: 600px) {
    #cart {
      position: absolute;
      top: 100%;
      z-index: 2;
      background-color: #EFEFEF;
      display: grid;
      justify-self: end;
      gap: 1vw;
      border-left: 5px solid #383838;
      border-bottom: 5px solid #383838;
    }

    .cart-products {
      place-items: center;
      grid-auto-columns: minmax(200px, 1fr);
    }

    .quantities {
      grid-auto-columns: minmax(60px, 1fr);
    }
  }

  #empty-cart {
    color: #0F0F0F;
    padding: 10px;
    text-align: center;
  }

/*====================
Modal
=====================*/

.modal {
  display: grid;
  place-items: center;
  position: fixed;
  z-index: 3;
  margin: 0;
  padding: 0;
  top: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

  .modal-window {
    display: grid;
    place-items: center;
    place-content: center;
    background-color: white;
    grid-auto-columns: minmax(40vw, 75vw);
    min-height: 20vh;
  }

.modal-images {
  max-width: 100%;
}

.modal p {
  padding: 2vw;
  text-align: center;
}

.modal button {
  margin: 3% 0;
}


/*====================
Info
=====================*/

.product-info {
  display: grid;
  justify-items: center;
  justify-content: center;
}

  h1 {
    font-size: 4rem;
    text-align: center;
  }

  .product-info > figure > button {
    background-color: unset;
    border: unset;
  }

  .product-info > figure > ol {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
  }

    figure > ol > li > button {
      background-color: unset;
      border: unset;
      padding: 5%;
    }

      figure > ol > li > button > img {
        border-bottom: 1px solid;
        padding-bottom: 5px;
      }

  .product-info > form > .rating {
    display: flex;
  }

  .product-info > form > .rating button {
    min-width: 50px;
  }

  .product-info p {
    width: 80%;
  }

  .product-info strong.prices {
    font-size: 2rem;
    padding-top: 5%;
    padding-bottom: 5%;
  }

  .product-info > .forms {
    display: grid;
    gap: 2vw;
  }

    .product-info .quantities {
      grid-column: 2;

      display: grid;
      grid-auto-flow: column;
      gap: 2vw;
    }

    .product-info .quantities button {
      display: grid;
    }

    .product-info > .forms > button {
      grid-column: span 2;

      max-width: 45vw;
      place-self: center;

      background-color: #9E1800;
      color: #EFEFEF;
    }

      #add-to-cart-span {
        display: grid;
        grid-template-columns: 70% 1fr;
        place-items: center;
      }

        #add-to-cart-span svg {
          width: 90%;
          padding: 5%;
        }


/*====================
Stars
=====================*/
main form button {
  width: 100%;
}

.stars {
  background-color: unset;
  border: unset;
}


/*====================
Images/Thumbs
=====================*/

@media (max-width: 349px) {
  #cart .thumbnails {
    width: 50%;
  }
}

@media (min-width: 350px) {
  #cart .thumbnails {
    width: 85%;
  }
}

@media (min-width: 600px) {
  #cart .thumbnails {
    height: 75px;
  }
}

/* main image */
.main-image {
  width: 100%;
}

/* thumbnails */
figure > ol > li > button > img {
  max-width: 100%;
}

/*====================
Reviews Info
=====================*/

.product-reviews {
  display: grid;
  place-self: center;
  grid-template-columns: 1fr 1fr;
  gap: 2vw 10%;
  margin: 5%;
}

  .product-reviews .stars {
    padding: 0;
  }

  .product-reviews > h2 {
    grid-column: span 2;
    font-size: 3rem;
  }

  .product-reviews .rating {
    display: grid;
    grid-auto-flow: column;
  }

  .reviews-container {
    grid-column: span 2;
  }

  .reviews-container > form {
    margin: 2vw;
  }

  #review-ratings-form {
    margin-bottom: 10%;
  }

  #review-ratings-form div {
    display: flex;
    align-items: center;
  }

    #review-ratings-form div > label {
      display: flex;
      align-items: center;
      margin-left: 10px;
    }

    #review-ratings-form div > label > svg {
      min-width: 40px;
    }

/*====================
User Reviews
=====================*/

article.reviews {
  background-color: #EAEAEA;
  border: 1px solid #E2E2E2;
  border-radius: 5px;

  margin-bottom: 10px;
  padding: 10px;

  display: grid;
  column-gap: 5%;
  grid-template-columns: 60% 35%;
  grid-template-rows: 15% 2fr 1fr;
}

  article.reviews > .names {
    margin: 0;
    align-self: center;
  }

  article.reviews > span {
    text-align: right;
  }

  article.reviews > .rating {
    grid-column: 1;

    display: grid;
    grid-template-columns: repeat(4, 25%);
  }

  article.reviews > .feedback {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    place-items: center;
  }

    .reviews > .feedback > button {
      background-color: unset;
      border: unset;
      padding: 0;
      width: 35%;
    }

    @media (max-width: 400px) {
      .reviews > .feedback > button {
        width: 60%;
      }
    }

  .reviews .stars {
    width: 80%;
  }

    @media (min-width: 400px) {
      .reviews .stars {
        width: 60%;
      }
    }


/*====================
Write Review
=====================*/

.write-review-form {
  display: flex;
  flex-flow: column;
  align-items: center;
}

.write-review-form > .titles {
  font-size: 2rem;
}

.write-review-form > .review-prompts {
  font-size: 1.5rem;
  margin: 5%;
}

  fieldset {
    width: 70vw;
    text-align: center;
    margin: 0;
    padding: 5%;
    border: 1.5px solid #E2E2E2;
    border-radius: 5px;
  }

  fieldset > div {
    display: flex;
    flex-flow: column;
    align-items: center;
    align-self: center;
  }

    fieldset > div > span {
      margin: 10px auto;
    }

    fieldset > div > .rating {
      display: flex;
      width: 60%;
    }

.write-review-form > button {
  width: 30%;
  margin-top: 5%;
  padding: 2%;
}

#review-submitted {
  font-size: 1.5rem;
  text-align: center;
}


/*====================
Edit
=====================*/

button#edit-review {
  grid-column: span 2;
  width: 40%;
  padding: 1%;
  margin: 5% 0;
  justify-self: center;
}

article.current-user {
  grid-template-rows: 15% 3fr 1fr;
}

article .write-review-form {
  grid-column: span 2;
}



/*====================
Footer
=====================*/

footer {
  margin-top: 20%;
  margin-bottom: 5%;
  text-align: center;
}